<template>
  <section class="news">
    <BackBtn class="news__backBtn"/>
    <h1 class="news__header">{{ t('news.title') }}</h1>
    <div
      v-for="(item, i) in newsData"
      :key="i"
      class="news__item"
      :class="{ news__item_active: expandNews === i }"
      @click="onExpand(i)"
    >
      <div class="news__item__header">
        <h2 class="news__item__title">{{ item.title }}</h2>
        <h3 class="news__item__date">{{ item.date }}</h3>
      </div>
      <p class="news__item__text">
        {{ item.text }}
        <span v-if="expandNews === i">{{ item.body }}</span>
      </p>
      <div v-if="expandNews === i" class="news__item__close">
        <Icon
          name="close"
          @click.stop="onExpand(-1)"
        />
      </div>
    </div>

    <h2
      @click="loadMoreNews"
      class="news__more-news"
    >{{ t('news.moreNews') }}</h2>
  </section>
</template>

<script>
import { useI18n } from 'vue-i18n'
import { ref, watch } from 'vue'
import moment from 'moment'
import { useActions } from '@/plugins/vuex'
import { MODULES_NAMES } from '@/common/modules'
import BackBtn from '@/components/UIKit/BackBtn'
import Icon from '@/components/Icon/Icon'

export default {
  name: 'News',
  components: { Icon, BackBtn },
  setup () {
    const { t, locale } = useI18n()
    const expandNews = ref(-1)
    const newsData = ref([
      {
        title: 'Merry Christmas!',
        globalDate: new Date(Date.now() - 86400000),
        date: moment(new Date(Date.now() - 86400000)).format('DD MMM YYYY'),
        text: 'Fees reduced to 1% till the 30th of December.',
        body: `

When you want to sign into your account, you are prompted to authenticate with a username and a password – that’s the first verification layer.

Two-factor authentication works as an extra step in the process, a second security layer, that will reconfirm your identity.
`
      },
      {
        title: 'Reimagine the products',
        globalDate: new Date(),
        date: moment(new Date()).format('DD MMM YYYY'),
        text: 'With the help of the Play Library, you can use Play early in your design process to build out fully functional pages with just a few taps.',
        body: `

When you want to sign into your account, you are prompted to authenticate with a username and a password – that’s the first verification layer.

Two-factor authentication works as an extra step in the process, a second security layer, that will reconfirm your identity.
`
      },
      {
        title: 'Merry Christmas!',
        globalDate: new Date(),
        date: moment(new Date()).format('DD MMM YYYY'),
        text: 'Fees reduced to 1% till the 30th of December.',
        body: `

When you want to sign into your account, you are prompted to authenticate with a username and a password – that’s the first verification layer.

Two-factor authentication works as an extra step in the process, a second security layer, that will reconfirm your identity.
`
      }
    ])

    const {
      setIsLoading
    } = useActions(MODULES_NAMES.LOADING)

    watch(locale, () => {
      updateDatesLocale()
    })

    const updateDatesLocale = () => {
      newsData.value.forEach(item => {
        item.date = moment(item.globalDate).format('DD MMM YYYY')
      })
    }

    const loadMoreNews = async () => {
      try {
        setIsLoading(true)
        const res = await loadNews()
        newsData.value = [...newsData.value, ...res.data]
      } catch (e) {
        console.log(e)
      } finally {
        setIsLoading(false)
      }
    }

    const loadNews = () => {
      return new Promise(resolve => {
        setTimeout(() => {
          const tempData = {
            data: newsData.value
          }
          resolve(tempData)
        }, 1000)
      })
    }
    const onExpand = (index) => {
      expandNews.value = index
    }

    return {
      t,
      newsData,
      expandNews,
      onExpand,
      loadMoreNews
    }
  }
}
</script>

